import React, { useState } from 'react'
import Panel from "../../Components/Panel"
import FadeInAnimation from '../../Components/FadeInAnimation'
import NextSectionArrow from '../../Components/NextSectionArrow'
import Tooltip from '@material-ui/core/Tooltip'
import KnowledgeDrawer from './KnowledgeDrawer'
import FormationDrawer from './FormationDrawer'
import './presentationSection.css'

export default function PresentationSection(props){

  const [formationDrawerOpen, setFormationDrawerOpen] = useState(false)
  const [knowledgeDrawerOpen, setKnowledgeDrawerOpen] = useState(false)

  return(
    <Panel id={props.id} backgroundColor='darkred'>
      <>
        <FadeInAnimation
          desktopAnimation={'FadeInLeftToRight'}
          mobileAnimation={'FadeInBottomUp'}
          wrapperClasses={'row no-gutters d-flex justify-content-center'}
        >
          <img className='col-12' id='profile-picture' alt='profile' src='./images/profile.jpeg' />

          <h1 className='col-12' id='profile-name' >
            Caio Augusto Ferreira Arrabal
          </h1>

          <div className='col-12 d-flex justify-content-center'>
            <Tooltip title='Formação'>
              <img
                src="./images/cap.svg"
                className='presentation-button'
                id='formation-button'
                alt='formation button'
                onClick={() => setFormationDrawerOpen(true)}/>
            </Tooltip>

            &nbsp;
            &nbsp;
            &nbsp;
            &nbsp;

            <Tooltip title='Conhecimentos'>
              <img
                src="./images/brainstorm.svg"
                className='presentation-button'
                id='knowledge-button'
                alt='knowledge preview'
                onClick={() => setKnowledgeDrawerOpen(true)}/>
              </Tooltip>
          </div>

          <NextSectionArrow goTo={props.nextSectionId} />

          <KnowledgeDrawer
            knowledgeDrawerOpen={knowledgeDrawerOpen}
            setKnowledgeDrawerOpen={setKnowledgeDrawerOpen}/>

          <FormationDrawer
            formationDrawerOpen={formationDrawerOpen}
            setFormationDrawerOpen={setFormationDrawerOpen}/>

        </FadeInAnimation>
      </>
    </Panel>
  )
}