import React, { useState } from 'react';
import handleViewport from 'react-in-viewport';
import PropTypes from 'prop-types';
import './fadeInAnimation.css';
import getClasses from './getTransitionClasses'

// Show the element only when it is on the screen
const Block = (props) => {
  const { inViewport, forwardedRef, showed } = props
  // const opacity = showed || inViewport ? 'opacity-100' : 'opacity-0'
  const className = getClasses(props.desktopAnimation, props.mobileAnimation, inViewport, showed, props)

  return (
    <div ref={forwardedRef} className={`${props.wrapperClasses} ${className}`} id={props.id}>
      {props.children}
    </div>
  );
};

const ViewportBlock = handleViewport(Block, /** options: {}, config: {} **/);

const Component = function FadeInAnimation(props){
  const [showed, setShowed] = useState(false); // Show the animation only one time

  return (
    <ViewportBlock showed={showed} onEnterViewport={() => {setShowed(true)}} {...props} />
  )
};

const animations = ['FadeInBottomUp', 'FadeInLeftToRight', 'FadeInRightToLeft']

Component.propTypes = {
  mobileAnimation: PropTypes.oneOf(animations).isRequired,
  desktopAnimation: PropTypes.oneOf(animations)
}

export default Component