export default function getClasses(desktopAnimation, mobileAnimation, inViewport, showed, props) {
    let classes = ''
    const animStatus = showed || inViewport ? 'end' : 'start'

    switch (desktopAnimation) {
        case 'FadeInBottomUp':
            classes += `desktop-fade-in-bottom-up-${animStatus} `;
            break;

        case 'FadeInLeftToRight':
            classes += `desktop-fade-in-left-right-${animStatus} `;
            break;

        case 'FadeInRightToLeft':
            classes += `desktop-fade-in-right-left-${animStatus} `;
            break;

        default:
    }

    switch (mobileAnimation) {
        case 'FadeInBottomUp':
            classes += `mobile-fade-in-bottom-up-${animStatus} `;
            break;

        case 'FadeInLeftToRight':
            classes += `mobile-fade-in-left-right-${animStatus} `;
            break;

        case 'FadeInRightToLeft':
            classes += `mobile-fade-in-right-left-${animStatus} `;
            break;
        
        default:
    }

    return classes
}
